@import url('https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800');
* {
  box-sizing: border-box;
}
body {
  /* background: #054480 !important; */
  background-image: url(image2.jpg);
  background-size: 1400px 850px;
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: 'Fira Sans', sans-serif;
}
h1,h2,h3,h4,h5,h6,label,span {
  font-weight: 500;
  font-family: 'Fira Sans', sans-serif;
}
body, html, .App, #root, .auth-wrapper {
  width: 100%;
  height: 100%;
}
.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(0, 0, 0, 0.2);
}
.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}
.auth-inner {
  width: auto;
  margin: auto;
  background-image: url(image.jpg);
  background-size: 1400px auto;
  /* background-image: linear-gradient(140deg, rgb(226, 217, 217),rgb(202, 180, 180),rgb(218, 207, 207)); */
  /* background: #ffffff; */
  box-shadow: 0px 20px 80px rgba(0, 0, 0, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 50px;
 
}
.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}
.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}
.custom-control-label {
  font-weight: 400;
}
.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #000000;
  margin: 0;
}
.forgot-password a {
  color: #167bff;
}
.containerw{
/* overflow-y: scroll; */
height: auto;
}